.modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  -webkit-animation-name: fadeIn;
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;

  &__window {
    height: 200px;
    padding: 0.5rem 0 2rem 0;
    padding: 0.5rem 1rem 2rem 2rem;
    font-size: 2rem;
    font-weight: bold;
    position: fixed;
    bottom: 0;
    background-color: whitesmoke;
    width: 100%;
    -webkit-animation-name: slideIn;
    -webkit-animation-duration: 0.4s;
    animation-name: slideIn;
    animation-duration: 0.4s;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    text-align: center;
  }
  &__wrap {
    box-sizing: content-box;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  &__pawn {
    width: 15%;
    padding: 1rem 0;
    font-size: 2rem;
    font-weight: normal;
    background-color: #fea6;
    border-radius: 5px;
    border: solid 2px black;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 15px 8px 46px -11px rgba(112, 109, 109, 0.75);
    transition: 0.5s;
    &:hover {
      background-color: black;
      color: rgba(255, 238, 170, 0.986);
      transition: 0.5s;
      cursor: pointer;
      box-shadow: 15px 8px 46px -11px black;
    }
  }
}

@keyframes slideIn {
  from {
    bottom: -300px;
    opacity: 0;
  }
  to {
    bottom: 0;
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
